import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/Home.vue";
import Build from "@/views/Build.vue";
import community from "@/views/community.vue";
import route from "@/views/route.vue";
import community1 from "@/views/community1.vue";
Vue.use(VueRouter);
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};
const routes = [
  // {
  //   path: "/",
  //   name: "Home",
  //   component: Home
  // },
  {
    path: "/",
    name: "try",
    component: () => import("@/views/buildNewStyle/try.vue"),
  },
  {
    path: "/BuildInfoPage",
    name: "Building",
    component: Build,
    children: [
      {
        path: "build1",
        component: () => import("@/views/build/build1/index.vue")
      },
      {
        path: "build2",
        component: () => import("@/views/build/build2/index.vue")
      },
      {
        path: "build3",
        component: () => import("@/views/build/build3/index.vue")
      },
      {
        path: "build9",
        component: () => import("@/views/build/build9/index.vue")
      },
      {
        path: "build25",
        component: () => import("@/views/build/build25-YeJi/index.vue")
      },
      {
        path: "YeJi",
        component: () => import("@/views/build/build25-YeJi/index.vue")
      },
      {
        path: "build23",
        component: () => import("@/views/build/build23/index.vue")
      },
      {
        path: "community",
        component: () => import("@/views/community1.vue")
      },
      // {
      //   path: "build15",
      //   component: () => import("@/views/build/build25/index.vue"),
      //   children: [
      //     {
      //       path: "/",
      //       name: "build1Detail",
      //       components: {
      //         build1Heng: build1Heng,
      //         build1Shu: build1Shu,
      //         default: build1Heng
      //       }
      //     }
      //   ]
      // },
      {
        path: "build2",
        component: () => import("@/views/build/build2/index.vue")
      }
      // {
      //   path: 'build3',
      //   component: () => import("@/views/build/build2/index.vue"),
      // }, {
      //   path: 'build4',
      //   component: () => import("@/views/build/build2/index.vue"),
      // },
    ]
  },
  {
    path: "/Community",
    name: "Community",
    component: community
  },
  {
    path: "/community1",
    name: "community1",
    component: community1
  },
  {
    path: "/route",
    name: "route",
    component: route
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});
export default router;
