<template>
  <div class="route">
    <div class="logo">
      <div class="west big" @click="change('west')" ref="west"></div>
      <div class="east small" @click="change('east')" ref="east"></div>
    </div>
    <div class="return" @click="back"></div>
    <div class="wrap">
      <!-- <img src="@/assets/route/west.png" alt="">
      <img src="@/assets/route/east.png" alt=""> -->
      <div class="wrap1">
        <div class="wrap2">
          <img src="@/assets/route/route.png" alt="" :style="{ transform: translate }" @click="jump">
        </div>
      </div>
      <div class="left" @mousedown="move('left')" @mouseup="up"></div>
      <div class="right" @mousedown="move('right')" @mouseup="up"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      flag1: true,
      flag2: true,
      speed: 10,
      translate: 'translate(0, 0)',
      translateV: 0,
      east: null,
      west: null,
      cur: 'west'
    };
  },
  components: {},
  mounted() {},
  methods: {
    change(dir) {
      if(dir === 'east' && this.cur === 'west') {
        this.flag1 = true
        this.translateV = -2524
        this.translate = `translate(-2524px, 0)`
        this.cur = 'east'
        this.east.className = 'east big'
        this.west.className = 'west small'
      }
      if(dir === 'west' && this.cur === 'east') {
        this.flag1 = true
        this.translateV = 0
        this.translate = `translate(0, 0)`
        this.cur = 'west'
        this.east.className = 'east small'
        this.west.className = 'west big'
      }
    },
    back() {
      this.$router.push('/')
    },
    jump(e) {
      let x = e.clientX - this.translateV
      if(this.translateV < -2520) {
        x -= 2524
      }
      let y = e.clientY
      console.log(x, y, this.cur)
      if(x > 150 && x < 190 && y > 430 && y < 535) {
        // console.log('业记')
      }
      if(x > 954 && x < 990 && y > 80 && y < 270 && this.cur === 'west') {
        // 大公报
        this.$router.push('/BuildInfoPage/build1')
      }
      if(x > 900 && x < 940 && y > 70 && y < 245 && this.cur === 'east') {
        // 米氏力奇
        this.$router.push('/BuildInfoPage/build2')
      }
      if(x > 2230 && x < 2270 && y > 340 && y < 475 && this.cur === 'west') {
        // 洋气豪宅
        this.$router.push('/BuildInfoPage/build3')
      }
      if(x > 260 && x < 300 && y > 390 && y < 510) {
        // 惠古东西
        this.$router.push('/BuildInfoPage/build9' && this.cur === 'east')
      }
      if(x > 2385 && x < 2422 && y > 395 && y < 610 && this.cur === 'west') {
        this.$router.push('/BuildInfoPage/build23')
      }
      if(x > 1505 && x < 1545 && y > 170 && y < 295 && this.cur === 'east') {
        // 业记甜汤
        this.$router.push('/BuildInfoPage/build25')
      }
    },
    move(type) {
      // let dir = type === 'left' ? 1 : -1
      this.flag2 = true
      console.log(type, this.translateV)
      if(this.flag1 === false && type === 'right') {
        this.flag1 = true
        this.translateV = -2524
        this.translate = `translate(-2524px, 0)`
        this.cur = 'east'
        this.east.className = 'east big'
        this.west.className = 'west small'
        return
      }
      if(this.flag1 === false && type === 'left') {
        this.flag1 = true
        this.translateV = -600
        this.translate = `translate(-600px, 0)`
        this.cur = 'west'
        this.east.className = 'east small'
        this.west.className = 'west big'
        return
      }
      if((this.translateV < 0 || type === 'right') && this.translateV > -3140) {
        if(type === 'left') {
          requestAnimationFrame(this.animationl)
        }
        if(type === 'right') {
          requestAnimationFrame(this.animationr)
        }
      }
      // this.translate = `translate(${this.translateV}px, 0)`
    },
    animationl() {
      this.translateV += 10
      if(this.translateV > 0) {
        this.translateV = 0
        return
      }
      if(this.translateV > -2520 && this.flag1 && this.translateV < -600) {
        this.flag1 = false
        return
      }
      this.translate = `translate(${this.translateV}px, 0)`
      if(this.flag2) {
        requestAnimationFrame(this.animationl)
      }
    },
    animationr() {
      this.translateV -= 10
      if(this.translateV < -600 && this.flag1 && this.translateV > -2520) {
        this.flag1 = false
        return
      }
      if(this.translateV <= -3134) {
        return
      }
      this.translate = `translate(${this.translateV}px, 0)`
      console.log(this.translateV)
      if(this.flag2) {
        requestAnimationFrame(this.animationr)
      }
    },
    up() {
      this.flag2 = false
    }
  },
  mounted() {
    this.east = this.$refs.east
    this.west = this.$refs.west
  }
};
</script>

<style scoped>
.logo{
  position: absolute;
  width: 200px;
  height: 200px;
  left: 0;
  top: 0;
  z-index: 100;
}
.return{
  position: absolute;
  z-index: 100;
  width: 150px;
  height: 150px;
  background-image: url('../assets/buildInfo/build25/返回主页logo.png');
  right: 2vw;
  bottom: 2vw;
  cursor: pointer;
}
.west{
  width: 133px;
  height: 141px;
  background-image: url('../assets/route/westbtn.png');
  transition: all 1s linear;
  cursor: pointer;
}
.east{
  width: 133px;
  height: 141px;
  background-image: url('../assets/route/eastbtn.png');
  transition: all 1s linear;
  cursor: pointer;
}
.small{
  position: absolute;
  transform: scale(0.5);
  left: 26%;
  bottom: 0;
  z-index: 100;
}
.big{
  position: absolute;
  transform: scale(0.8);
  z-index: 101;
  left: 0;
}
.route{
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}
.wrap{
  /* height: 100vh; */
  display: flex;
  /* overflow-x: scroll; */
}
img{
  height: 100vh;
  transition: all .3s linear;
}
.wrap1{
  width: 269.3vh;
}
.wrap2{
  overflow: hidden;
  width: 269.3vh;
}
.left{
  width: 42px;
  height: 125px;
  position: absolute;
  left: 1vw;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 1;
  background-image: url('../assets/route/left.png');
  cursor: pointer;
}
.right{
  width: 42px;
  height: 125px;
  position: absolute;
  right: 1vw;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 1;
  background-image: url('../assets/route/right.png');
  cursor: pointer;
}
</style>
