<template>
  <div id="community">
    <div class="logo">
      <router-link to="/">
        <img src="@/assets/community/community_logo.png" alt=""
      /></router-link>
    </div>
    <div class="wrap" ref="target" :style="{ overflowY: scrollY }">
      <div class="scroll" @mousewheel="scroll" :style="{ height: height }">
        <img src="@/assets/community/recommend.png" alt="" class="recommend" @click="toRoute">
        <div class="opacity" :style="{ height: height }"></div>

        <div class="wrap1" :style="{ display: display1 }">
          <img src="@/assets/community/community1.png" alt="">
        </div>
        <div class="wrap2" :style="{ position: img2P, transform: transform2, display: display2 }" ref="img2">
          <img src="@/assets/community/community2.png" alt="">
        </div>
        <div class="wrap3" :style="{ position: img3P, transform: transform3, marginTop: margin3, display: display3 }" ref="img3">
          <img src="@/assets/community/community3.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { throttle } from '@/utils/index.js'
export default {
  data() {
    return {
      transform2: 'translate(0, 0)',
      transform3: 'translate(0, 0)',
      tag1: 7250,
      tag2: 7300,
      tag3: 9450,
      tag4: 9550,
      img2: null,
      img3: null,
      margin3: '2vw',
      img1P: 'relative',
      img2P: 'fixed',
      img3P: 'fixed',
      display1: 'block',
      display2: 'block',
      display3: 'block',
      target: null,
      cur: 0,
      height: [0, 0, 0],
      height: '870vh',
      scrollY: 'scroll'
    }
  },
  methods: {
    toRoute() {
      this.$router.push('/route')
    },
    scroll: function(e) {
      console.log(this.target.scrollTop, e.wheelDeltaY)
      if(this.target.scrollTop < this.tag4 && this.img3P === 'relative' && e.wheelDeltaY === 120) {
        this.transform3 = 'translate(0, 0)'
        this.scrollY = 'hidden'
        this.img3.addEventListener('transitionend', () => {
          this.display2 = 'block'
          this.target.scrollTop = 2300
          this.tag4 = 9550
          this.img3P = 'fixed'
          this.scrollY = 'scroll'
          this.height = '300vh'
          this.margin3 = '2vw'
        })
        return
      }

      if(this.target.scrollTop < this.tag2 && this.img2P === 'relative' && e.wheelDeltaY === 120) {
        this.transform2 = 'translate(0, 0)'
        this.scrollY = 'hidden'
        this.img2.addEventListener('transitionend', () => {
          this.display1 = 'block'
          this.target.scrollTop = 7250
          this.tag2 = 7300
          this.tag3 = 9450
          this.img2P = 'fixed'
          this.scrollY = 'scroll'
          this.height = '870vh'
        })
        return
      }

      if(this.target.scrollTop >= this.tag1 && this.img2P === 'fixed' && e.wheelDeltaY === -120) {
        this.transform2 = 'translate(0, -4vw)'
        this.img2.addEventListener('transitionend', () => {
          this.display1 = 'none'
          this.tag2 = 100
          this.tag3 = 2300
          this.target.scrollTop = 0
          this.img2P = 'relative'
          this.height = '100vh'
        })
        return
      }

      if(this.target.scrollTop >= this.tag3 && this.img3P === 'fixed' && e.wheelDeltaY === -120) {
        this.transform3 = 'translate(0, -2vw)'
        this.img3.addEventListener('transitionend', () => {
          this.display2 = 'none'
          this.tag4 = 100
          this.target.scrollTop = 0
          this.img3P = 'relative'
          this.height = '300vh'
          this.margin3 = '-1vw'
        })
        return
      }
    }
  },
  mounted() {
    this.target = this.$refs.target
    this.img2 = this.$refs.img2
    this.img3 = this.$refs.img3
  }
}
</script>

<style scoped>
#community{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
.opacity{
  position: absolute;
  z-index: 9999;
  opacity: 0;
  width: 100%;
  height: 800vh;
}
.wrap1{
  position: relative;
  margin-left: 13vw;
  margin-top: 2vw;
  margin-bottom: 2vw;
  z-index: 100;
  border-radius: 0.5rem;
  background: rgba(142, 167, 144, 0.8);
}
.wrap2{
  /* right: 6vw; */
  width: 87vw;
  top: 1vw;
  z-index: 10;
  margin-left: 7vw;
  /* margin-bottom: 2vw; */
  margin-top: 3vw;
  border-radius: 0.5rem;
  background: rgba(218, 229, 240, 0.8);
  transition: all 1s linear;
}
.wrap3{
  right: 0vw;
  /* right: 1vw; */
  top: 0vw;
  z-index: 1;
  margin-left: 13vw;
  margin-top: 2vw;
  border-radius: 0.5rem;
  background: rgba(249, 202, 154, 0.8);
  transition: transform 1s linear;
}
.wrap{
  position: absolute;
  width: 101vw;
  height: 100vh;
  /* background: rgba(142, 167, 144, 0.8); */
}
.scroll{
  height: 800vh;
}
img{
  width: 87vw;
  background-size: 100% 100%;
  /* transition: transform .2s linear; */
}
.logo{
  position: absolute;
  z-index: 10001;
}
.logo img:nth-child(1) {
  width: 10rem;
  position: fixed;
  top: 2rem;
  left: 2rem;
  z-index: 10000;
  cursor: pointer;
}
/* .logo img:nth-child(2) {
  position: absolute;
  right: 2rem;
  bottom: 2rem;
  z-index: 10000;
  width: 135px;
  height: 120px;
} */
.recommend{
  position: absolute;
  top: 735vh;
  right: 44vw;
  width: 40rem;
  cursor: pointer;
  z-index: 10001;
}
</style>