<template>
  <div class="community" @mousewheel="handleMousewhell">
    <div class="card-0 card cur" ref="card0">
      <div class="content">
        <img src="@/assets/community/community1.png" alt="" class="main" />
        <router-link
          to="/route
        "
        >
          <img src="@/assets/community/recommend.png" alt="" class="route"
        /></router-link>
      </div>
    </div>
    <div class="card-1 card next" ref="card1">
      <div class="content">
        <img src="@/assets/community/community2.png" alt="" class="main" />
        <img src="@/assets/community/recommend.png" alt="" class="route" />
      </div>
    </div>
    <div class="card-2 card" ref="card2">
      <div class="content">
        <img src="@/assets/community/community3.png" alt="" class="main" />
        <img src="@/assets/community/recommend.png" alt="" class="route" />
      </div>
    </div>
    <div class="logo">
      <router-link to="/">
        <img src="@/assets/community/community_logo.png" alt=""
      /></router-link>
      <img src="@/assets/buildInfo/build25/地图logo.png" alt="" />
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      cur: 0,
      srollY: 0,
      layerLevel: [0, 1, 2],
      curhigh: []
    };
  },
  components: {},
  // mounted() {set},
  methods: {
    handleMousewhell(e) {
      this.srollY += e.wheelDeltaY;
      if (this.srollY >= 0) this.srollY = 0;
      this.$refs[`card${this.cur}`].style.setProperty(
        "--x",
        this.srollY + "px"
      ); //设置x
      this.isMoveOut(e);
    },
    isMoveOut(e) {
      let bottom = this.$refs[`card${this.cur}`].getBoundingClientRect().bottom; //获取元素底部距离视口的距离
      let top = this.$refs[`card${this.cur}`].getBoundingClientRect().top;
      // console.log(bottom);
      if (bottom <= 0) {
        //用户向下滚动时
        if (this.cur == 2) return;

        this.curhigh[this.cur] = this.srollY; //暂存滚动高度，这样向上滚动（返回时）也能回到当前位置

        this.cur = (this.cur + 1) % 3;

        this.srollY = 0;
      }
      if (top >= 0 && e.wheelDeltaY > 0) {
        //用户向上滚动时
        if (this.cur == 0) return;
        this.cur = Math.abs(this.cur - 1) % 3;
        this.srollY = this.curhigh[this.cur];
      }
    }
  },
  watch: {
    cur: function() {
      // for (let i = 0; i < 3; i++) {
      //   this.$refs[`card${i}`].classList.remove("cur");
      //   if (i == this.cur) this.$refs[`card${i}`].classList.add("cur");
      // }
      let layerLevel = [];
      if (this.cur == 0) layerLevel = [0, 1, 2];
      if (this.cur == 1) layerLevel = [1, 2, 0];
      if (this.cur == 2) layerLevel = [2, 1, 0];

      for (let i = 0; i < 3; i++) {
        this.$refs[`card${i}`].classList.remove("cur");
        this.$refs[`card${i}`].classList.remove("next");
      }
      this.$refs[`card${layerLevel[0]}`].classList.add("cur");
      this.$refs[`card${layerLevel[1]}`].classList.add("next");
    }
  }
};
</script>

<style lang="scss" scoped>
.community {
  .card {
    width: 87vw;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    // overflow:scroll;
    transform: translateY(var(--x));
    border-radius: 0.5rem;
  }
  .content {
    .main {
      object-fit: cover;
      width: 100%;
    }
    .route {
      width: 40rem;
      position: absolute;
      bottom: 3rem;
      left: 3rem;
    }
  }
  .card-0 {
    background: rgba(142, 167, 144, 0.8);
    top: 2rem;
    left: 13rem;
  }
  .card-1 {
    background: rgba(218, 229, 240, 0.8);
    top: 3rem;
    left: 7rem;
  }
  .card-2 {
    background: rgba(249, 202, 154, 0.8);
    top: 4rem;
    left: 15rem;
  }
  .cur {
    z-index: 3;
  }
  .next {
    z-index: 2;
  }
  .logo {
    img:nth-child(1) {
      width: 10rem;
      position: fixed;
      top: 2rem;
      left: 2rem;
      z-index: 4;
    }
    img:nth-child(2) {
      position: absolute;
      right: 2rem;
      bottom: 2rem;
      z-index: 4;
    }
  }
}
</style>
