<template>
  <div
    class="swiper-container"
    @mousewheel="handleMousewhell"
    @mouseover="handleMouseOver"
    @mouseout="handleMouseOut"
  >
    <div class="swiper-wrapper" :style="trans">
      <div
        class="swiper-slide"
        :style="{ height: slideHeight + 'vh' }"
        v-for="val in srcs"
        :key="val.index + 'a'"
      >
        <router-link :to="{ path: val.link, query: { index: val.index } }">
          <img :src="val.imgSrc" />
        </router-link>
      </div>

      <div
        class="swiper-slide"
        :style="{ height: slideHeight + 'vh' }"
        v-for="val in srcs"
        :key="val.index + 'b'"
      >
        <router-link :to="{ path: val.link, query: { index: val.index } }">
          <img :src="val.imgSrc" />
        </router-link>
      </div>

      <div
        class="swiper-slide"
        :style="{ height: slideHeight + 'vh' }"
        v-for="val in srcs"
        :key="val.index + 'c'"
      >
        <router-link :to="{ path: val.link, query: { index: val.index } }">
          <img :src="val.imgSrc" />
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { throttle } from "@/utils/index.js";
export default {
  name: "slider",
  data() {
    return {
      duration: 750, //速度
      index: 0,
      // forbid: false,
      trans: "transform: translateY(-100vh)",
      slideHeight: 14.2857,
      loopId: null,
    };
  },
  mounted: function () {
    setTimeout(() => {
      this.loop(); //开启是否滚动
    }, this.delay);

    this.slideHeight = 16.6667;
  },
  methods: {
    handleMousewhell: throttle(function (event) {
      // if (this.forbid) return;

      // let site = 0;
      if (event.wheelDeltaY > 0) {
        this.index--;
        if (this.index == -7) {
          this.index = 0;
          this.trans = `transition:linear 0ms;transform:translateY(${
            -100 - (this.index + 1) * 14.2857
          }vh)`;
        }
        let site = -100 - this.index * 14.2857;
        setTimeout(() => {
          this.trans = `transition:linear ${this.duration}ms;transform:translateY(${site}vh)`;
        }, 20);
        console.log(this.index);
      }
      if (event.wheelDeltaY < 0) {
        this.index++;
        if (this.index == 7) {
          console.log("触发");
          this.index = 0;
          this.trans = `transition:linear 0ms;transform:translateY(${
            -100 - (this.index - 1) * 14.2857
          }vh)`;
        }
        let site = -100 - this.index * 14.2857;
        setTimeout(() => {
          this.trans = `transition:linear ${this.duration}ms;transform:translateY(${site}vh)`;
        }, 20);
      }
    }, 50),
    loop: function () {
      if (this.loopId) return;
      if (this.direction == 0) {
        this.loopId = setInterval(() => {
          //向下
          this.index++;
          if (this.index == 7) {
            this.index = 0;
            this.trans = `transition:linear 0ms;transform:translateY(${
              -100 - (this.index - 1) * 14.2857
            }vh)`;
          }
          let site = -100 - this.index * 14.2857;
          setTimeout(() => {
            this.trans = `transition:linear ${this.duration}ms;transform:translateY(${site}vh)`;
          }, 17);
        }, this.duration);
      } else {
        this.loopId = setInterval(() => {
          //向上
          this.index--;
          if (this.index == -7) {
            this.index = 0;
            this.trans = `transition:linear 0ms;transform:translateY(${
              -100 - (this.index + 1) * 14.2857
            }vh)`;
          }
          let site = -100 - this.index * 14.2857;
          setTimeout(() => {
            this.trans = `transition:linear ${this.duration}ms;transform:translateY(${site}vh)`;
          }, 17);
        }, this.duration);
      }

      // if(this.direction==0){
      // setInterval(() => {
      // this.index--;
      //   console.log(this.index);
      //   if (this.index == -2) {
      //     this.trans = `transition:0ms;transform:translateY(-6000px)`;
      //     this.index = 8;
      //   }
      //   let site= -1500 - this.index * 500;
      //   setTimeout(() => {
      //     this.trans = `transition:${
      //       this.duration * 2
      //     }ms;transform:translateY(${site}px)`;
      //   }, 50);
      // }, 1000);
      // }
      // else{
      // setInterval(() => {
      //   this.index++;
      //   if (this.index == 10) {
      //     this.trans = `transition:0ms;transform:translateY(-1000px)`;
      //     this.index = 0;
      //   }
      //   let site = -1500 - this.index * 500;
      //   setTimeout(() => {
      //     this.trans = `transition:${
      //       this.duration * 2
      //     }ms ;transform:translateY(${site}px)`;
      //   }, 50);
      // }, 1000);
      // }
    },
    handleMouseOver: function () {
      if (this.loopId) {
        clearInterval(this.loopId);
        this.loopId = null;
      }
      let site = -100 - this.index * 14.2857;
      this.trans = `transition:ease-out ${
        this.duration * 2
      }ms;transform:translateY(${site + 0.1}vh)`;
    },
    handleMouseOut: function () {
      this.loop();
    },
  },
  props: ["delay", "direction", "layer", "srcs"],
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.swiper-container {
  width: 500px;
  height: 100%;
  overflow: hidden;
  /* overflow: scroll; */
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  height: 14.2857vh;
  width: 100%;
  background: rgba(0, 0, 0, 0);
  /* border:4px solid black; */
  display: flex;
  justify-content: center;
  align-items: center;
  a {
    height: 100%;
    img {
      height: 80%;
      // max-width: 80%;
      object-fit: contain;
    }
  }
}

.swiper-wrapper {
  /* transition: 500ms; */
}
</style>
